/* ---- Page Link All PAGES  ---- */
.page-template {
  font-family: 'Raleway', sans-serif;
  height: 100%;
}
.page-template header,
.page-template footer {
  display: none;
}
.page-template .all-pages-wrap {
  background-image: url('https://unsplash.it/g/1600/900?random');
  background-color: #eee;
  background-attachment: fixed;
  background-position: center center;
  background-size: cover;
  height: 100%;
  display: table;
  width: 100%;
}
.page-template .all-pages-wrap:before {
  display: block;
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  z-index: 1;
  opacity: 1;
  -webkit-animation-duration: 5s;
  animation-duration: 5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}
.page-template .all-pages-wrap .header-star-wars {
  margin: 0 0 40px;
}
.page-template .all-pages-wrap .header-star-wars h1 {
  color: #FFF;
  margin: 0;
  text-transform: uppercase;
  font-weight: 700;
}
.page-template .all-pages-wrap .btn-star-wars {
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  background-color: transparent;
  border-radius: 4px;
  border: 0;
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.3);
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-weight: 300;
  line-height: 40px;
  padding: 0 25px;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  float: right;
}
.page-template .all-pages-wrap .btn-star-wars:hover,
.page-template .all-pages-wrap .btn-star-wars:active,
.page-template .all-pages-wrap .btn-star-wars:focus {
  box-shadow: inset 0 0 0 1px #5480f1;
  color: #5480f1;
}
.page-template .all-pages-wrap .btn-star-wars .fa {
  margin-right: 10px;
}
.page-template .all-pages-wrap .template-wrap {
  vertical-align: middle;
  display: table-cell;
  padding: 30px 0;
}
.page-template .all-pages-wrap .template-wrap .container-center {
  background: rgba(23, 24, 32, 0.95);
  opacity: 1;
  padding: 50px 0;
  border-top: 7px solid #5480f1;
  position: relative;
  z-index: 2;
}
.page-template .all-pages-wrap .template-wrap .container-center .container {
  overflow: hidden;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block {
  border: 1px solid #f5f5f5;
  padding: 20px 30px;
  border-radius: 3px;
  margin-bottom: 20px;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block.animated1 {
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block.animated2 {
  -webkit-animation-duration: 2.5s;
  animation-duration: 2.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .black-title {
  color: #FFF;
  margin: 0 0 20px;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .black-title span:after {
  background: #FFF;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .double-arrow-list {
  margin-bottom: 0;
  width: 200px;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .double-arrow-list li > a {
  color: #FFF;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .double-arrow-list li > a:hover {
  color: #5480f1;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .double-arrow-list li > a:after {
  color: #FFF;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block label {
  color: #FFF;
  padding-right: 10px;
  cursor: default;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .form-control {
  -moz-transform: scale(1);
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  -moz-transition: border-color 0.2s ease, background-color 0.2s ease;
  -webkit-transition: border-color 0.2s ease, background-color 0.2s ease;
  -ms-transition: border-color 0.2s ease, background-color 0.2s ease;
  transition: border-color 0.2s ease, background-color 0.2s ease;
  background-color: transparent;
  border-radius: 6px;
  border: none;
  border: solid 2px rgba(255, 255, 255, 0.35);
  color: inherit;
  outline: 0;
  padding: 0 1em;
  text-decoration: none;
  margin-right: 10px;
  color: #fff;
  height: 40px;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .form-control:-webkit-autofill {
  background-color: transparent;
  background-image: none;
  border-color: #5480f1;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .form-control:focus {
  -moz-animation: focus 0.1s;
  -webkit-animation: focus 0.1s;
  -ms-animation: focus 0.1s;
  animation: focus 0.1s;
  background-color: rgba(255, 255, 255, 0.1);
  border-color: #5480f1;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .btn.btn-primary {
  color: white;
  background-color: #5480f1;
}
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .btn.btn-primary:focus,
.page-template .all-pages-wrap .template-wrap .container-center .container .teal-block .btn.btn-primary:hover {
  background-color: rgba(84, 128, 241, 0.8);
}
.double-arrow-list li > a {
  position: relative;
  color: white;
  height: 35px;
  box-sizing: content-box;
  display: block;
  line-height: 35px;
  padding-right: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.double-arrow-list li > a:after {
  content: "\00BB" /* » */;
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  line-height: 35px;
  color: black;
  opacity: .4;
}
.double-arrow-list li > a:hover,
.double-arrow-list li > a:active,
.double-arrow-list li > a:focus {
  font-weight: bold;
  text-decoration: none;
}
.double-arrow-list li > a:hover:after,
.double-arrow-list li > a:active:after,
.double-arrow-list li > a:focus:after {
  opacity: 1;
  color: white;
  font-weight: normal;
}
.double-arrow-list li.active a {
  font-weight: bold;
}
.double-arrow-list li.active a:after {
  opacity: 1;
  color: white;
  font-weight: normal;
}
.double-arrow-list.thin li > a {
  font-size: 18px;
  height: auto;
  overflow: visible;
  text-overflow: none;
  white-space: normal;
  line-height: 1.1;
  margin-bottom: 9px;
}
.double-arrow-list.thin li > a span {
  border-bottom: 1px solid #eee;
  line-height: auto;
  display: inline-block;
  padding-bottom: 9px;
  width: calc(100% - 40px - 60px);
}
.double-arrow-list.thin li > a:after {
  opacity: 1;
  color: rgba(0, 0, 0, 0.4);
  border-bottom: 1px solid #eee;
  vertical-align: top;
  display: inline-block;
  width: 40px;
  text-align: center;
  line-height: 1.1;
  height: 100%;
}
.double-arrow-list.thin li > a:hover {
  font-weight: normal;
}
.double-arrow-list.thin li > a:hover:after {
  color: black;
}
.bullet-list {
  padding-left: 30px;
}
.bullet-list > li {
  margin-bottom: 7px;
}
.bullet-list > li:before {
  font: normal normal normal 14px/1 FontAwesome;
  content: "\f111";
  display: inline-block;
  vertical-align: top;
  width: 25px;
  font-size: 10px;
  padding-top: 7px;
}
.bullet-list > li a {
  display: inline-block;
  font-size: 18px;
  border-bottom: 1px solid;
  padding-bottom: 5px;
  max-width: calc(100% - 25px - 4px);
}
.bullet-list > li a:hover {
  text-decoration: none;
}
.black-numbers {
  counter-reset: item;
}
.black-numbers > li {
  padding-left: 5px;
}
.black-numbers > li span {
  display: inline-block;
  width: calc(100% - 30px - 20px);
  vertical-align: top;
}
.black-numbers > li + li {
  margin-top: 35px;
}
.black-numbers > li:before {
  display: inline-block;
  vertical-align: middle;
  content: "0" counter(item);
  counter-increment: item;
  width: 30px;
  margin-right: 20px;
  font-size: 48px;
  direction: rtl;
  line-height: 1.1;
  border-bottom: 3px solid;
  font-weight: 100;
  letter-spacing: -3px;
}
.body-guide {
  background-color: #2e3141;
}
.body-guide .main-wrap {
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
}
.body-guide .block-affix {
  position: fixed;
}
.body-guide .block-affix > .nav-stacked > li + li {
  margin-top: 0;
  margin-left: 0;
}
.body-guide .block-affix > .nav-pills > li > a {
  border-radius: 0;
  width: 200px;
  font-weight: 600;
  color: #FFF;
  transition: 0.3s;
}
.body-guide .block-affix > .nav-pills > li.active > a,
.body-guide .block-affix .nav-pills > li.active > a:focus,
.body-guide .block-affix > .nav-pills > li.active > a:hover {
  color: #fff;
  background-color: #5480f1;
}
.body-guide .block-affix > .nav > li > a:focus,
.body-guide .block-affix .nav > li > a:hover {
  text-decoration: none;
  color: #5480f1;
  background-color: #eee;
}
.section {
  display: table;
  width: 100%;
  table-layout: fixed;
  border-top: 1px solid #dfe1e8;
}
.col {
  display: table-cell;
  padding: 3rem;
  vertical-align: top;
}
.col + .col {
  background-color: #f7f7f9;
}
.page-header {
  margin: 0 0 10px;
}
.main-wrap {
  margin-left: 200px;
  background: #F0F0F0;
}
.title-page {
  margin: 0;
  padding: 3rem;
  background: #4c5c96;
  color: #FFF;
  font-weight: 600;
}
.list-page {
  background: #5480f1;
  margin-bottom: 0;
  width: 200px;
  border-bottom: 1px solid #FFF;
  height: 99px;
}
.list-page li.active a,
.list-page li:hover a {
  font-weight: 600;
  background: #FFF;
  color: #5480f1;
  text-decoration: none;
}
.list-page a {
  color: #FFF;
  text-align: center;
  display: block;
  padding: 2px;
}
pre {
  border: 0;
  background: transparent;
  padding: 0;
}
.cp {
  color: #009999;
}
.nt {
  color: #2f6f9f;
}
.na {
  color: #4f9fcf;
}
.s {
  color: #d44950;
}
